.DateContainer {
  display: flex;
  align-items: top;
  justify-content: space-between;
  padding: 0 10px;
}

.DayOfWeek {
  font-size: 9vw;
}

.MonthAndDate {
  font-size: 10vw;
}
.CustomMessage {
  padding-left: 20px;
  margin: 10px 10px 10px 0;
  font-size: 3vw;
  z-index: 2;
  max-width: 50%;
}
.TimeContainer {
  font-size: 26vw;
  position: relative;
  top: 12vh;
}
.TempContainer {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}


.Temperature {
  display: flex;
  align-items: flex-start;
  font-size: 16vw;
  position: relative;
  left: 1.5vw;
  top: 5vh;
}

.DegreeSymbol {
  font-size: 9vw;
  position: relative;
  right: 1vw;
  top: 1vh;
}

.TempRangeContainer {
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
}

.LowTemp, .HighTemp {
  font-size: 3vw;
  margin: 0 2px;
}

.TempRangeBar {
  align-self: center;
  height:2vw;
  width: 60%;
  background-color: black;
  border-radius: 15px;

}
.SolarContainer{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  position: relative;
  top: .5vh;
}

.SunriseContainer, .SunsetContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.SunriseContainer {
  margin-right: 1rem;
}

.SunriseContainer svg, .SunsetContainer svg{
  width: 3rem;
}

.SunriseTime, .SunsetTime {
  font-size: 2.2rem;
}

.SunriseTime span, .SunsetTime span {
  font-size: 1.6rem;
  margin-left: .1rem;
}

.ShortForecastContainer {
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  right: 1vw;
}

.ForecastIcon {
  font-size: 14vw;
}

.ShortForecastText {
  font-size: 3vw;
  display: flex;
  justify-content: center;
  position: relative;
  right: .5vw;
  bottom: 1vh;
}
