.SolarContainer{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  position: relative;
  top: .5vh;
}

.SunriseContainer, .SunsetContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.SunriseContainer {
  margin-right: 1rem;
}

.SunriseContainer svg, .SunsetContainer svg{
  width: 3rem;
}

.SunriseTime, .SunsetTime {
  font-size: 2.2rem;
}

.SunriseTime span, .SunsetTime span {
  font-size: 1.6rem;
  margin-left: .1rem;
}
