.TempContainer {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}


.Temperature {
  display: flex;
  align-items: flex-start;
  font-size: 16vw;
  position: relative;
  left: 1.5vw;
  top: 5vh;
}

.DegreeSymbol {
  font-size: 9vw;
  position: relative;
  right: 1vw;
  top: 1vh;
}

.TempRangeContainer {
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
}

.LowTemp, .HighTemp {
  font-size: 3vw;
  margin: 0 2px;
}

.TempRangeBar {
  align-self: center;
  height:2vw;
  width: 60%;
  background-color: black;
  border-radius: 15px;

}